
<template>
	<div style="padding:10px;">
		<el-row>
			<el-col :span="9">
				<el-input v-model="searchKey" @keyup.enter.native="search" placeholder="请输入基金经理名字、基金名字或公司名字"></el-input>
			</el-col>
			<el-col :span="3">
				<el-button @click="search">搜索</el-button>
			</el-col>
			<el-col :span="3">
				<el-button type="text" @click="switchMode">切换到高级模式</el-button>
			</el-col>
		</el-row>
		<br>
		<manager-list  :managers="managers"
					   :pageCount="pageCount"
					   :pageNum="pageNum"
					   @page-change="pagechange" />
	</div>
</template>
<script>
  import { get,post }  from '../../common/function/service';
  import managerList  from './managerList.vue';

  export default {
      data() {
        return {
          searchKey:'',
          managers:[],
          pageNum:1,
          pageCount:0,
        }
      },
      created() {

      },
      computed: {

      },

      methods: {
        search: function () {
		  post('/finance/manager/search',{
            searchKey: this.searchKey,
            pageSize:20,
            pageNum:this.pageNum
          }).then(res=>{
            if(res.code === 0){
              this.managers = res.data.map(m=>{
                return {
                  name: m.name,
                  code: m.code,
                  company: m.company,
                  mainFund: m.managedFundDtoList && m.managedFundDtoList.reduce((re,cur)=>re&&re.amount>=cur.amount?re:cur,null)
                };
              });
              this.pageCount  = res.pageDto["pages"];
            }
          });
        },
        pagechange: function (index) {
          this.pageNum = index;
          this.search();
        },
        switchMode:function () {
          this.$emit("switch-mode")
        },
      },
      components: {
        managerList:managerList,
      },
    }
</script>
<style lang="scss" scoped>
	.type-label{
		width: 60px;
		font-size: 16px;
		display: inline-block;
	}
	.fund-text{
		text-decoration: underline;
		&:hover{
			cursor: pointer;
		}
	}
</style>

