
<template>
	<div style="padding:10px;">
		<div>
			<label class="type-label">适应性: </label>
			<el-radio-group v-model="selection.marketType">
				<el-radio :label="'牛市'">牛市</el-radio>
				<el-radio :label="'熊市'">熊市</el-radio>
				<el-radio :label="'震荡市'">震荡市</el-radio>
				<el-radio :label="null">不选</el-radio>
			</el-radio-group>
		</div>
		<br>
		<div>
			<label class="type-label">风格: </label>
			<el-select v-model="selection.style1">
				<el-option :value="'成长'" />
				<el-option :value="'价值'" />
				<el-option :label="'不选择'" :value="null"/>
			</el-select>
			<el-select v-model="selection.style2" >
				<el-option :value="'大盘'" />
				<el-option :value="'小盘'" />
				<el-option :label="'不选择'" :value="null"/>
			</el-select>
		</div>
		<br>
		<div>
			<label class="type-label">行业: </label>
			<el-select v-model="selection.industry">
				<el-option v-for="item in industries" :key="item.swlevel1code" :value="item.swlevel1" />
				<el-option key="all" :value="null"  :label="'全部行业'"/>
			</el-select>
		</div>
		<br>
		<el-button @click="filter" >筛选</el-button>
		<el-button type="text" @click="switchMode" style="padding-left: 40px">切换到简单模式</el-button>

		<br>
		<manager-list  :managers="managers"
					   :pageCount="selection.pageCount"
					   :pageNum="selection.pageNum"
					   @page-change="pagechange" />
	</div>
</template>
<script>
  import { get,post }  from '../../common/function/service';
  import managerList  from './managerList.vue';

  export default {
      data() {
        return {
          industries:[
            {swlevel1code:1,swlevel1:''},
          ],
          managers:[
          ],
          selection:{
            marketType:null,
            style1:null,
            style2:null,
            industry:null,
            pageNum:1,
            pageCount: 0,
          }
        }
      },
      created() {
		get('/finance/config/swcodes').then(res=>{
          this.industries = res.data;
        });

        let selection = this.$store.state.selection;
        if(selection){
          for(let key of Object.keys(selection)){
            this.selection[key] = selection[key]
          }
          this.filter();
		}

      },
      computed: {

      },

      methods: {
		filter: function () {
          window.store.commit('SAVE_SELECTION', this.selection);
		  return post('/finance/manager/list',{
			adaptive: this.selection.marketType,
			style1: this.selection.style1,
			style2: this.selection.style2,
			swlevel1: this.selection.industry,
            pageSize:20,
            pageNum:this.selection.pageNum
		  }).then(res=>{
		    if(res.code === 0){
              this.managers = res.data.map(m=>{
                return {
                  name: m.name,
                  code: m.code,
									company: m.company,
                  mainFund: m.managedFundDtoList && m.managedFundDtoList.reduce((re,cur)=>re&&re.amount>=cur.amount?re:cur,null)
                };
              });
              this.selection.pageCount  = res.pageDto["pages"];
			}
		  });
		},
        pagechange: function (index) {
		  this.selection.pageNum = index;
		  this.filter();
        },
        switchMode:function () {
          window.store.commit('SAVE_SELECTION', null);

          this.$emit("switch-mode")
        },
      },
      components: {
        managerList: managerList,
      },
    }
</script>
<style lang="scss" scoped>
	.type-label{
		width: 60px;
		font-size: 16px;
		display: inline-block;
	}
	.fund-text{
		text-decoration: underline;
		&:hover{
			cursor: pointer;
		}
	}
</style>

