<template>
	<div style="padding:10px;">
		<search-content v-if="mode==0" @switch-mode="switchMode"/>
		<filter-content v-if="mode==1" @switch-mode="switchMode"/>
	</div>
</template>
<script>
  import  filterContent   from './filterContent.vue';
  import  searchContent  from './searchContent.vue';
  export default {
      data() {
        return {
          mode: this.$store.state.selection?1:0
        }
      },
      created() {

      },
      computed: {

      },

      methods: {
        switchMode: function(){
          this.mode = (this.mode+1)%2;
		},
      },
      components: {
        filterContent:filterContent,
        searchContent:searchContent
      },
    }
</script>


