
<template>
	<div style="padding:10px;">
		<table-plus v-if="managers&&managers.length>0"
				  :data="managers"
				>
			<el-table-column type="index" :index="indexMethod" />
			<el-table-column  prop="name" label="名字" width="150" >
				<template slot-scope="scope">
					<div class="fund-text" @click="gotoDetail(scope.row.code, scope.row.name)" >{{scope.row.name}}</div>
				</template>
			</el-table-column>
			<el-table-column  prop="company" label="公司名字" width="150" />
			<el-table-column  prop="mainFund" label="主力基金" width="300" >
				<template slot-scope="scope">
					<div class="fund-text" @click="gotoFundDetail(scope.row.mainFund.fundCode,scope.row.mainFund.fundName)">
						<span v-if="scope.row.mainFund">{{scope.row.mainFund.fundName}}</span>
						<span v-if="scope.row.mainFund">{{scope.row.mainFund.fundCode}}</span>
					</div>
				</template>
			</el-table-column>
			<el-table-column label="详情" width="70" >
				<template slot-scope="scope">
					<el-button  size="mini" @click="gotoDetail(scope.row.code,scope.row.name)">...</el-button>
				</template>
			</el-table-column>
			<el-table-column label="操作" width="150" >
				<template slot-scope="scope">
					<el-button  size="mini" @click="exportPdf(scope.row.code)">生成pdf</el-button>
				</template>
			</el-table-column>
		</table-plus>
		<div class="block" v-if="pageCount">
			<el-pagination layout="prev, pager, next"
						   :page-count="pageCount"
						   :current-page="pageNum"
						   @current-change="pagechange"/>
		</div>
	</div>
</template>
<script>
  import { get,post }  from '../../common/function/service';
  export default {
	  props: {
        managers: Array,
        pageNum:Number,
        pageCount: Number,
	  },
      created() {
      },
      computed: {

      },

      methods: {
		gotoDetail: function(code,name){
          this.$router.push({path: `/manager/${code}/${name}`})
		},
        gotoFundDetail: function(code,name){
          this.$router.push({path: `/fund/${code}/${name}`})
        },
        indexMethod: function(index) {
          return index + 1 + 20*(this.pageNum-1);
        },
        pagechange: function (index) {
		  this.$emit("page-change",index)
        },
        exportPdf(code) {
          get(`/finance/manager/${code}/pdf`).then(res=>{
            this.$alert('正在为您生成pdf格式的报告，请耐心等候。生成完毕后会发到你的邮箱', '提示', {
              confirmButtonText: 'OK',
            });
          });
        },
      },
      components: {
      },
    }
</script>
